import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icClose from '@iconify/icons-ic/twotone-close';
import icPrint from '@iconify/icons-ic/twotone-print';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPerson from '@iconify/icons-ic/twotone-person';
import icMyLocation from '@iconify/icons-ic/twotone-my-location';
import icLocationCity from '@iconify/icons-ic/twotone-location-city';
import icEditLocation from '@iconify/icons-ic/twotone-edit-location';
import { Item } from '../interfaces/item.model';

import { NgForm } from '@angular/forms';
import { ProductService } from '../../../../shared/services/product.service';
import { Product } from '../../../../shared/models/product';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { firestore } from 'firebase';
import { StorageService } from '../../../../shared/services/storage/storage.service';

declare var toastr: any;
declare var $: any;
@Component({
  selector: 'vex-item-create-update',
  templateUrl: './item-create-update.component.html',
  styleUrls: ['./item-create-update.component.scss']
})
export class ItemCreateUpdateComponent implements OnInit {

  productoWeb: Product = new Product();
  productList: Product[];
  productObject: Product;
  loading = false;

  static id = 100;

  form: FormGroup;
  mode: 'create' | 'update' = 'create';

  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icPhone = icPhone;

  constructor(
    @Inject(MAT_DIALOG_DATA) 
    public defaults: any,
    private dialogRef: MatDialogRef<ItemCreateUpdateComponent>,
    private fb: FormBuilder,
    private firestore: AngularFirestore,
    private storageService: StorageService,
    private productoService: ProductService
    ) {}

  ngOnInit() {
    if(this.storageService.getSession("$key")!=null){
      this.productoWeb = this.storageService.getSession("mydata")     
    }

    if (this.defaults) {
      this.mode = 'update';
    } else {
      this.defaults = {} as Product;
    }

    const newId = this.firestore.createId();
    this.storageService.setSession("mykey", newId)

    this.form = this.fb.group({

      $key: newId,
      id: [ItemCreateUpdateComponent.id++],
      producto: [this.defaults.producto|| ''],
      tipo: [this.defaults.tipo || ''],
      costo: [this.defaults.costo || ''],
    });
  }


  save(ProductForm: NgForm) {
    if (this.mode === 'create') {
      this.addProduct(ProductForm);
    } else if (this.mode === 'update') {
      this.updateItems();
    }
  }

  addProduct(ProductForm: NgForm) {
    this.productoService.postProduct(ProductForm.value);
    this.createItem();
    alert("Producto guardado")

  }



  createItem() {
    const item = this.form.value;

    if (!item.imageSrc) {
      item.imageSrc = 'assets/img/avatars/1.jpg';
    }

    this.dialogRef.close(item);
  }

  updateItems(){
    if(this.storageService.getSession("$key")!=null){
      //var value = new AddEstablishment
        var values = { 
          producto: this.productoWeb.producto,
          tipo: this.productoWeb.tipo,    
          costo: this.productoWeb.costo,

        }
  
        const Item = this.form.value;  
        Item.producto = String(values.producto)
        Item.tipo = String(values.tipo)
        Item.costo = String(values.costo)
        this.productoService.updateProduct(values)
        this.updateItem()
        console.log('print value::'+ JSON.stringify(Item));
      }else{
        console.log('no entro')
      }
  }

  updateItem() {
    const item = this.form.value;
    item.id = this.defaults.id;

    this.dialogRef.close(item);
  }

  isCreateMode() {
    return this.mode === 'create';
  }

  isUpdateMode() {
    return this.mode === 'update';
  }
}
