  updateItem(i: Product) {
    console.log(i);
    this.storageService.setSession("mydata", i);
    this.storageService.setSession("$key", i.$key)
    

    this.dialog.open(ItemCreateUpdateComponent, {
      
      data: i
      
    }).afterClosed().subscribe(updatedItem => {
      
      if (updatedItem) {    
        const index = this.items.findIndex((existingItem) => existingItem.id === updatedItem.id);
        this.items[index] = new Product();
        this.subject$.next(this.items);
      }
    });

    

  }
  

  removeAt(i){     
    console.log(i);

      this.productoService.deleteProduct(i);
      
      this.items.splice(this.items.findIndex((existingItem) => existingItem.id === i.id), 1);
      this.selection.deselect(i);
      this.subject$.next(this.items);
  }

